<template>
  <div>
    nero
  </div>
</template>

<script>
export default {
  data() {
    //   验证邮箱
    var checkEmail = (rule, value, cb) => {
      const regEmail = /^([a-zA-z0-9_-])+@([a-zA-z0-9_-])+(\.[a-zA-z0-9_-])+/
      if (regEmail.test(value)) {
        return cb()
      }
      cb(new Error('输入的邮箱不合法请重新输入'))
    }
    //   验证手机号
    var checkMobile = (rule, value, cb) => {
      const regMobile = /^(0|86|17951)?(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57])[0-9]{8}$/
      if (regMobile.test(value)) {
        return cb()
      }
      cb(new Error('输入的手机号不合法请重新输入'))
    }
    return {
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 2
      },
      userList: [],
      total: 0,
      addUserVisible: false,
      allotRolesVisible: false,
      addForm: {
        username: '',
        password: '',
        mobile: '',
        email: ''
      },
      addFormRules: {
        username: [
          { required: true, message: '请输入用户名', trigger: 'blur' },
          {
            min: 3,
            max: 10,
            message: '用户名长度需要在3到10个字符之间',
            trigger: 'blur'
          }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          {
            min: 6,
            max: 15,
            message: '密码长度需要在6到15个字符之间',
            trigger: 'blur'
          }
        ],
        mobile: [
          { required: true, message: '请输入电话', trigger: 'blur' },
          { validator: checkMobile, trigger: 'blur' }
        ],
        email: [
          { required: true, message: '请输入邮箱', trigger: 'blur' },
          { validator: checkEmail, trigger: 'blur' }
        ]
      },
      editUserVisible: false,
      editForm: {},
      editFormRules: {
        mobile: [
          { required: true, message: '请输入电话', trigger: 'blur' },
          { validator: checkMobile, trigger: 'blur' }
        ],
        email: [
          { required: true, message: '请输入邮箱', trigger: 'blur' },
          { validator: checkEmail, trigger: 'blur' }
        ]
      },
      //角色列表
      rolesList:[],
      roleValue:''
    }
  },
  created() {
    this.getUserList()
  },
  methods: {
    async getUserList() {
      const { data: res } = await this.$http.get('users', {
        params: this.queryInfo
      })
      if (res.meta.status != 200) {
        return this.$message.error('获取用户列表失败!')
      }
      this.total = res.data.total
      this.userList = res.data.users
    }, //修改显示条数
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getUserList()
    }, //修改页码
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getUserList()
    }, //更改用户状态事件
    async userStateChange(userinfo) {
      const { data: res } = await this.$http.put(
        `users/${userinfo.id}/state/${userinfo.mg_state}`
      )
      if (res.meta.status != 200) {
        userinfo.mg_state = !userinfo.mg_state
        return this.$message.error('修改用户状态失败')
      }
      console.log(1)

      this.$message.success('修改用户状态成功')
    },
    // 关闭新增用户表单重置表单
    addDialogClosed() {
      this.$refs.addFormRef.resetFields()
    }, //添加用户
    addUser() {
      this.$refs.addFormRef.validate(async valid => {
        // 判断如果预效验通过则发送请求添加用户
        if (valid == true) {
          const { data: res } = await this.$http.post('users', this.addForm)
          // 如果返回的状态码不是201就是添加失败
          if (res.meta.status != 201) {
            this.$message.error('添加用户失败')
          } else {
            this.$message.success('添加用户成功')
            // 隐藏添加表单
            this.addUserVisible = false
            // 重新获取数据
            this.getUserList()
          }
        }
      })
    }, //展示需要修改的用户信息
    async editUserShow(id) {
      const { data: res } = await this.$http.get('users/' + id)
      this.editForm = {
        username: res.data.username,
        id: res.data.id,
        mobile: res.data.mobile,
        email: res.data.email
      }
      this.editUserVisible = true
    }, // 关闭新增用户表单重置表单
    editDialogClosed() {
      this.$refs.editFormRef.resetFields()
    }, //修改用户
    editUser() {
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) {
          return this.$message.error('表单验证没有通过')
        }
        const { data: res } = await this.$http.put(
          'users/' + this.editForm.id,
          this.editForm
        )
        console.log(res)

        if (res.meta.status != 200) {
          return this.$message.error('更新用户信息失败')
        }
        this.$message.success('更新用户信息成功')
        this.editUserVisible = false
        this.getUserList()
      })
    }, //删除用户
    async removeUser(id) {
      const flag = await this.$confirm(
        '此操作将永久删除该用户, 是否继续?',
        '提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }
      ).catch(err => err)
      if (flag != 'confirm') {
        return this.$message('取消了删除用户')
      }
      const { data: res } = await this.$http.delete('users/' + id)
      if (res.meta.status != 200) {
        return this.$message.error('删除用户失败')
      }
      this.$message.success('删除用户成功')
      this.getUserList()
    }, //弹出分配用户角色对话框
    async allotRoles() {
      const {data:res} = await this.$http.get('roles')
      this.rolesList = res.data
      this.allotRolesVisible = true
    }
  }
}
</script>